import React, {Component, Fragment} from 'react';
import {connect} from 'react-redux';

class MessageBar extends Component{

    componentWillReceiveProps(nextProps){
    //    (!this.props.realtime.realtime) ?this.message = nextProps.processing.message:this.message = nextProps.realtime.message ;
    this.message = nextProps.messageBar.message;
    }

    render(){
        var video = (
            <div className='messageBar'>
                    <p>
                        <img style={{width:45}} src={this.props.messageBar.icon}/>
                        {this.message}
                        {this.props.messageBar.link ?(<a href={this.props.messageBar.link}> Link </a>):''}
                    </p>
            </div>  
            );
        return(
            
            <Fragment>
                  {video}
            </Fragment>
        );
    }
}

const mapStateToProps = state => ({
    messageBar: state.MessageBar,
    processing: state.Processing,
    realtime:state.RealTime
});

export default connect(
    mapStateToProps,
    null
)(MessageBar);