import axios from 'axios';
import Loading from '../Assets/loading.gif';
import Alert from '../Assets/alert.png';

const hostname = 'http://localhost:5000/'

export const fileProcesssing = data => dispatch =>{
    dispatch({
        type:'displayMessage',
        payload:{message: 'Processing File',icon:Loading}
    })
    const url = hostname+'apiv1/startProcess/'+data.filename;
    axios
    .get(url)
    .then(res=>{
        console.log(res.data);
        // recieveWS(res.data)(dispatch)
        if(!res.data.error){
            dispatch({
                type:'displayMessage',
                payload:{message:res.data.message}
            })
            dispatch({
                type: 'processDone',
                payload: res.data
            });
        }
        if(res.data.error){
            dispatch({
                type:'displayMessage',
                payload:{message:res.data.message, icon:Alert}
            })
            dispatch({
                type: 'errorProcessing',
                payload: res.data
            });
        }
    })
    .catch(err=>{
        dispatch({
            type: 'displayMessage',
            payload: {message:err.message, icon:Alert}
        })
    })
}



