import axios from "axios";
import Alert from '../Assets/alert.png';
export const togglePlayPause = (playback, realtime) =>  dispatch=>{
    if(!playback.isPlaying){
        dispatch({
        type: 'Play/Pause',
        payload: 1
    });
    }
    else{
        dispatch({
        type: 'Play/Pause',
        payload: 0
    });
    }
    if(realtime.realtime){
        if(playback.isPlaying){
            axios
            .get('http://localhost:5000/ondevice/pause')
            .then(res=>{
                console.log(res.data.status)
            })
            .catch(err=>{
                dispatch({
                    type: 'displayMessage',
                    payload: {message:err.message, icon:Alert}
                })
            })
        }
        else{
            axios
            .get('http://localhost:5000/ondevice/play')
            .then(res=>{
                console.log(res.data.status)
                dispatch({
                    type:'setSources'
                })
            })
            .catch(err=>{
                dispatch({
                    type: 'displayMessage',
                    payload: {message:err.message, icon:Alert}
                });
                dispatch({
                    type:'Play/Pause',
                    payload:0
                });
            })
        }
    }
}

export const stop = (playback, realtime) =>  dispatch =>{
    dispatch({
        type: 'Stop'
    });

    if(realtime.realtime){
        axios
        .get('http://localhost:5000/ondevice/stop')
        .then(res=>{
            console.log(res.data.status)
            dispatch({
                type:'resetSource'
            })
            if (playback.isRecording){dispatch({
                type: 'displayMessage',
                payload: {message: "Download Recorded file", icon:null, link:'http://localhost:5000/apiv1/record/'+res.data.file}
            })}
        })
        .catch(res=>{
            dispatch({
                type: 'displayMessage',
                payload: {message:res.message, icon:null}
            })
        })
    }
}

export const record = (playback,realtime) =>  dispatch =>{
    dispatch({
        type: 'Record'
    })
    dispatch({
        type: 'displayMessage',
        payload: {message:'Recording', icon:null}
    })
    if(!playback.isPlaying){
        dispatch({
            type:'Play/Pause',
            payload:1
        });
    }
    if(realtime.realtime){
        axios
        .get('http://localhost:5000/ondevice/record')
        .then(res=>{
            console.log(res.data.status)
        })
        .catch(res=>{
            dispatch({
                type: 'displayMessage',
                payload: {message:res.message, icon:null}
            });
            dispatch({
                type:'Play/Pause',
                payload:0
            });
        })
    }
}