import axios from "axios";

export const renderUploadForm = e => dispatch =>{
    axios.get('http://localhost:5000/ondevice/stop');
    dispatch ({
        type: 'resetRealTime'
    });
    dispatch({
        type: 'uploadform',
        payload: {display: true, uploadform:true}
    })
}

export const hideForm = () => dispatch =>{
    dispatch({
        type: 'resetAll'
    })
}